.header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  text-align: center;
  max-width: 880px;
  margin: 0 auto;
  padding: 45px 0 41px 0;
  border-bottom: 1px solid rgba(84, 84, 84, 0.7);
}

@media screen and (max-width: 450px) {
  .header {
    flex-wrap: wrap;
  }
}